@media screen and (-webkit-min-device-pixel-ratio:0) {
    input[type='range'] {
        overflow: hidden;
        width: 225px;
        margin: 20px;
        -webkit-appearance: none;
        background-color: #d4d4d0;
    }

    input[type='range']::-webkit-slider-runnable-track {
        height: 12px;
        -webkit-appearance: none;
        color: #723ee7;
        margin-top: -1px;
    }

    input[type='range']::-webkit-slider-thumb {
        width: 10px;
        -webkit-appearance: none;
        height: 12px;
        cursor: ew-resize;
        background: #282727;
        box-shadow: -500px 0 0 500px #723ee7;
    }
}

