.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#hymnal-header, #songs-header{
  text-align: center;
}

#hymn-list, #hymnal-list {
  text-align: left;
  text-underline: none;
}

a, u {
  text-decoration: none !important;
  color: black;
}

ul {
  list-style-type: none;
  padding-top: 0;
  padding-left: 5%;
}

a:visited {
  text-decoration: none;
  color: black;
}

#hymn-list li, #hymnal-list li, #churches-list li, #homepage-list li, #menu-list li {
  padding: 4px;
  white-space: nowrap;
  text-align: left;
}

.this-week-table {
  padding: 4px;
  border: 1px black;
  text-align: left;
}

#this-week-div {
  margin-top: 10px;
}
